import React from 'react';
import styles from './login.module.css';
import Login_nav from '../../components/login_header/login_nav';
import App_description from '../../components/app_description/app_description';
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Login = ({ authService, userRepository }) => {
  let navigate = useNavigate();
  useEffect(() => {
    authService.onAuthChange((user) => {
      if (user) {
        userRepository.checkUser(user.uid, (userData) => {
          if (userData) {
            navigate('/app/home');
          } else {
            navigate('/makeUser');
          }
        });
      } else {
      }
    });
  });
  return (
    <section className={styles.login}>
      <section className={styles.background}>
        <Login_nav />
        <main className={styles.loginMain}>
          <h1 className={styles.mainTitle}>
             .  ,   
            .
          </h1>
          <h2 className={styles.mainDescription}>
                 .
          </h2>
          <ul className={styles.loginBtns}>
            <li>
              <button
                onClick={() => {
                  authService.login('Google');
                }}
                className={styles.loginBtn}
              >
                 
              </button>
            </li>
            {/* <li>
              <button className={styles.loginBtn}> </button>
            </li> */}
          </ul>
        </main>
      </section>
      {/* <App_description /> */}
    </section>
  );
};

export default Login;
